@font-face {
	font-family: Museo Sans Rounded;
	font-weight:300;
	src: url("/assets/fonts/MuseoSansRounded-300.otf") format("opentype");
}

@font-face {
	font-family: Museo Sans Rounded;
	font-weight: 500;
	src: url("/assets/fonts/MuseoSansRounded-500.otf") format("opentype");
}

html, * {
	box-sizing: border-box;
}

body {
	background-color:#f5f6f9;
	font-family:"Museo Sans Rounded",Museo Sans Rounded;
	font-size:16px;
	line-height:22px;
	color:#333;
	font-weight:300;
}

.center {
	text-align:center;
}
.rule {
	border-bottom:1px solid #e0e0e0;
	padding-bottom:0.8em;
}
.light {
	color:#fff;
}
.subtle {
	opacity:0.5;
}

.on {
	display:block !important;
}
.clear:after {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}

a {
	font-weight:500;
	color:#339933;
}
a:hover,
a:focus {
	color:#333;
}

a.close {
	display:inline-block;
	text-indent:-9999em;
	width:13px;
	height:13px;
	background:url("/assets/images/icons/close.svg") no-repeat center center;
	opacity:0.5;
}
a.close:hover,
a.close:focus {
	opacity:0.8;
}

p,ul,ol,dl {
	margin-bottom:1em;
}

p {
	max-width:40em;
}

h1 {
	font-size:28px;
	margin-bottom:0.5em;
}
h2 {
	font-size:24px;
	margin-bottom:0.6em;
}
h3 {
	font-size:20px;
	margin-bottom:0.8em;
}
h4 {
	font-size:16px;
	margin-bottom:1em;
}
h5 {
	font-size:14px;
	text-transform:uppercase;
	margin-bottom:1em;
}
h1,h2,h3,h4,h5 {
	font-weight:500;
	line-height:1.2em;
}

ul {
}
ul li {
	background:url("/assets/images/bullet.svg") no-repeat 0 0.2em;
	background-size:0.8em;
	padding-left:1.2em;
}

ul.alt li {
	background-size:1.2em;
	background-position:0 0;
	padding-left:2em;
	margin-bottom:0.7em;
}

ol {
	list-style: decimal;
}

dl {
	border-top:1px solid #e0e0e0;
	list-style:none;
}
dl li {
	border-bottom:1px solid #e0e0e0;
	overflow:hidden;
	padding:0.6em 0 0.6em 10em;
}
dl dt {
	font-weight:500;
	width:10em;
	float:left;
	margin-left:-10em;
}
dl dd {}

code,.mono {
	font-family:"Consolas","Andale Mono",monospace;
	font-size:12px;
	color:#666;
}
code {
	display:inline-block;
	border:1px solid #e0e0e0;
	background:#f6f6f6;
	padding:0.5em;
	border-radius:4px;
}
pre {
	display:block;
	max-width:100%;
	margin-bottom:2.5em;

}
pre code {
	max-width:100%;
	overflow:scroll;
	display:block;
}

hr {
	border:0;
	margin:2em 0;
	border-top:1px solid #e0e0e0;
}

.meta {
	font-size:14px;
	line-height:19px;
}
.big {
	font-size:18px;
	line-height:26px;
}


@media (min-width: 768px) {

	h1 {
		font-size:36px;
		margin-bottom:0.5em;
	}
	h2 {
		font-size:28px;
		margin-bottom:0.6em;
	}
	h3 {
		font-size:22px;
		margin-bottom:0.8em;
	}
	h4 {
		font-size:17px;
		margin-bottom:1em;
	}

}

@media (min-width: 900px) {

	h1 {
		font-size:48px;
		margin-bottom:0.5em;
	}
	h2 {
		font-size:36px;
		margin-bottom:0.6em;
	}
	h3 {
		font-size:24px;
		margin-bottom:0.8em;
	}
	h4 {
		font-size:18px;
		margin-bottom:1em;
	}

}
